import CommonCardFrame from '@/components/CommonCardFrame';
import { deleteEmptyProperty } from "../../../../../utils/utils";
import ArticleCategoryCascader from '@/components/Cascader/Article/Category';
export default {
  components: {
    CommonCardFrame: CommonCardFrame,
    ArticleCategoryCascader: ArticleCategoryCascader
  },
  data: function data() {
    return {
      form: this.$form.createForm(this, {
        name: 'noticeFilterForm'
      }),
      categoryOptions: []
    };
  },
  computed: {
    defaultCategory: function defaultCategory() {
      return [];
    }
  },
  methods: {
    handleSearchClick: function handleSearchClick(e) {
      var params = this.form.getFieldsValue();

      if (params.category_id !== undefined) {
        params.category_id = params.category_id[params.category_id.length - 1];
      }

      deleteEmptyProperty(params);
      this.$emit('search', params);
    },
    handleResetClick: function handleResetClick() {
      this.form.resetFields();
    }
  }
};